import React, { useState, useEffect } from 'react'
import BigNumber from 'bignumber.js'
import Web3 from 'web3'
import { ethers } from 'ethers'
import useToast from 'hooks/useToast'
import { useModal } from '@pancakeswap-libs/uikit'
import { getBalanceString, getDecimalAmount, formatNumber, getBalanceAmount } from 'utils/formatBalance'

import { useActiveWeb3React } from '../../hooks'
import * as FarmContract from '../../config/abis/Farm.json'
import * as IBEP20Contract from '../../config/abis/IBEP20.json'
import {
  FormInfo,
  StackDataContainer,
  SmallStackDataContainer,
  Label,
  BigLabel,
  Value,
  ColoredValue,
  Details,
  BlackChevronDown,
  BlackChevronUp,
  MediumStackDataContainer,
  LogoContainer,
  Logo,
  FirstLogo,
  SecondLogo,
  FormContainer,
  DetailsContainer,
  FarmsDetails,
  FarmDetailsData,
  FarmDetailsActions,
  FarmDetailsRow,
  FormActionButton,
} from './styleds'
import { addressesByChain } from './data'
import DepositWithdrawModal from './DepositWithdrawModal'

const Farm = (props) => {
  /* * STATE * */
  const { farmData, eseedPrice, eggplantPrice, bnbPrice } = props
  const {  _pid, tokenOverride, token1, token2  } = farmData
  const [showMore, setShowMore] = useState(false)
  const [showModal, setShowModal] = useState(false)

  const { toastSuccess, toastError } = useToast()

  const { account, chainId } = useActiveWeb3React()
  const [farmEnabled, setFarmEnabled] = useState(false)
  const [farmEnabledPending, setFarmEnabledPending] = useState(false)

  const [farmContract, setFarmContract] = useState({})
  const [lpTokenContract, setLpTokenContract] = useState({})

  const [lpTokenBalance, setLpTokenBalance] = useState('0')
  const [stakingBalance, setStakingBalance] = useState('0')
  const [eseedEarned, setEseedEarned] = useState('0')
  const [totalStaked, setTotalStaked] = useState('0')
  const [apr, setApr] = useState('0')
  const [renderKey, setRenderKey] = useState('0')
  const [harvestLoading, setHarvestLoading] = useState(false)

  /* ** CONSTANTS ** */
  // Production
  const inactive = false

  useEffect(() => {
    async function loadWeb3() {
      if (window.alreadyAlerted){
        return;
      }
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum)
        await window.ethereum.enable()
      } else if (window.web3) {
        window.web3 = new Web3(window.web3.currentProvider)
      } else {
        window.alert('Incompatible browser detected. Please use Metamask or Trust wallet to properly load this page.')
        window.alreadyAlerted = true;
      }
    }

    async function loadData() {
      const web3 = window.web3
      const farmAddress = addressesByChain.FARM[chainId]
      const lpAddress = farmData.addressByChain[chainId]
      /* **** ********* **** */
      /* **** LOAD FARM **** */
      /* **** ********* **** */
      const farmContractData = await new web3.eth.Contract(FarmContract.abi, farmAddress)
      setFarmContract(farmContractData)

      // Load User staking balance
      const userData = await farmContractData.methods.userInfo(_pid, account).call()
      setStakingBalance(userData.amount)

      // Load User Eseed Earned
      const userEseedEarned = await farmContractData.methods.pendingEseed(_pid, account).call()
      setEseedEarned(userEseedEarned)

      /* **** ************* **** */
      /* **** LOAD LP TOKEN **** */
      /* **** ************* **** */
      const lpToken = new web3.eth.Contract(IBEP20Contract.abi, lpAddress)
      setLpTokenContract(lpToken)

      const lpTokenBalanceData = await lpToken.methods.balanceOf(account).call()
      setLpTokenBalance(lpTokenBalanceData)

      // check for total staked in farm.
      const stakedInFarm = await lpToken.methods.balanceOf(farmAddress).call()

      // check for approval
      const farmAllowance = await lpToken.methods.allowance(account, farmAddress).call()
      setFarmEnabled(farmAllowance > 0)




      // Calculate APR
      // APR is year return / total staked.
      // yearReturn = eseedPerShare * sharesLocked * blocksPerMonth * monthsPerYear * pricePerEseed

      // GET USD released per year per lp token
      const { allocPoint }  = await farmContractData.methods.poolInfo(_pid).call()
      const totalAllocPoint  = await farmContractData.methods.totalAllocPoint().call()
      const eseedPerBlock  = await farmContractData.methods.eseedPerBlock().call()

      const eseedPerBlockForPool = getBalanceAmount(eseedPerBlock).multipliedBy(allocPoint/totalAllocPoint);
      
      const usdPerYear = eseedPerBlockForPool.multipliedBy(28800*365).multipliedBy(eseedPrice)

      // GET value of one LP token.
      // get bnbBalanceOf lp token contract
      const bnbContract = new web3.eth.Contract(IBEP20Contract.abi, '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c')
      const bnbBalanceOfLP = getBalanceAmount(await bnbContract.methods.balanceOf(lpAddress).call())
      const lpTokenTotalSupply = getBalanceAmount(await lpToken.methods.totalSupply().call())

      // get value of one LP = bnbBalance * bnbPrice / totalSupply
      let lpValue = bnbBalanceOfLP.multipliedBy(bnbPrice).dividedBy(lpTokenTotalSupply)
      if (_pid === 2){
        // Handle LP value if we're looking at eggplant-eseed stake.
        const eggPlantContract = new web3.eth.Contract(IBEP20Contract.abi, '0x1583ffe1720027500ba6b284b1b1e9755d43e479')
        const egpBalanceOfLP = getBalanceAmount((await eggPlantContract.methods.balanceOf(lpAddress).call()), 9)
        lpValue = egpBalanceOfLP.multipliedBy(eggplantPrice).dividedBy(lpTokenTotalSupply)
      }
      
      const totalValueLocked = getBalanceAmount(stakedInFarm).multipliedBy(lpValue);
      // APR is wrong. 
      // APR is value recieved per year / value locked 
      // usdPerYearPerShare / lpValue
      const aprr = usdPerYear.dividedBy(totalValueLocked)
      setApr(getBalanceString(getDecimalAmount(aprr).multipliedBy(100)))
      setTotalStaked(getBalanceString(getDecimalAmount(totalValueLocked)))

    }
    loadWeb3().then(() => {
      loadData()
    })
  }, [account, renderKey, chainId, farmData.addressByChain, _pid, eseedPrice, eggplantPrice, token1, bnbPrice])

  const onDismiss = async () => {
    setShowModal(false)
  }

  const HandleEnable = async () => {
    const maxUint256 = ethers.constants.MaxUint256
    const spender = farmContract.options.address
    try {
      setFarmEnabledPending(true)
      await lpTokenContract.methods.approve(spender, maxUint256).send({ from: account })
      toastSuccess('Contract Enabled', 'You can now stake in the pool')
    } catch (e) {
      toastError('Error', 'Please try again. Confirm the transaction and make sure you are paying enough gas!')
      console.log(e)
    }
    setFarmEnabledPending(false)
    setRenderKey(Math.random())
  }

  const HandleDeposit = async (amount) => {
    const amountToDeposit = getDecimalAmount(amount)
    let message = `Staked! Your funds have been staked in the pool!`
    try {
      setHarvestLoading(true)
      if (amount === 0) {
        message = `Successfully harvested ESeeds`
      }
      await farmContract.methods.deposit(_pid, amountToDeposit.toFixed(0).toString()).send({ from: account })
      toastSuccess(message)
      setRenderKey(Math.random())
      onDismiss()
    } catch (e) {
      console.log(e)
      toastError('Canceled', 'Please try again and confirm the transaction.')
    }
    setHarvestLoading(false)
  }

  const HandleWithdraw = async (amount) => {
    console.log("amount", amount)
    const amountToWithdraw = getDecimalAmount(amount)
    console.log("amount", amountToWithdraw)
    console.log("amount", amountToWithdraw.toFixed(0).toString())
    let message = `Unstaked! Your LP Tokens are back in your wallet!`
    try {
      if (amount === 0) {
        message = `Successfully unstaked your tokens`
      }
      await farmContract.methods.withdraw(_pid, amountToWithdraw.toFixed(0).toString()).send({ from: account })
      toastSuccess(message)
      setRenderKey(Math.random())
      onDismiss()
    } catch (e) {
      console.log(e)
      toastError('Canceled', 'Please try again and confirm the transaction.')
    }
    setHarvestLoading(false)
  }

  const tokenLabel = tokenOverride || `${token1}-${token2}`

  const [onDeposit] = useModal(
    <DepositWithdrawModal
      max={lpTokenBalance}
      title={`Stake ${tokenLabel}`}
      onDismiss={onDismiss}
      onConfirm={HandleDeposit}
      tokenName={tokenLabel}
      addLiquidityUrl="#/pool"
    />
  )

  const [onWithdraw] = useModal(
    <DepositWithdrawModal
      max={stakingBalance}
      title={`Unstake ${tokenLabel}`}
      onDismiss={onDismiss}
      onConfirm={HandleWithdraw}
      tokenName={tokenLabel}
      addLiquidityUrl="#/pool"
    />
  )
  
  return (
    <FormContainer inactive={!showMore}>
      <FormInfo inactive={inactive}>
        <LogoContainer>
          {
            tokenOverride ? 
            <FirstLogo src={`/images/logo-${tokenOverride.toLowerCase()}.png`} /> : 
            <span>
              <FirstLogo src={`/images/coins/${token1.toLowerCase()}.png`}/>
              <SecondLogo src={`/images/coins/${token2.toLowerCase()}.png`} />
            </span>
          }
        </LogoContainer>
        <StackDataContainer>
          {
            tokenOverride ? 
            <BigLabel>Stake {tokenOverride}</BigLabel> : 
            <BigLabel>{`${token1}-${token2}`}</BigLabel>
          }
          <ColoredValue>Earn ESEED</ColoredValue>
        </StackDataContainer>
        <StackDataContainer>
          <Label inactive={inactive}>ESEED Earned</Label>
          <ColoredValue inactive={inactive}>{getBalanceString(eseedEarned)}</ColoredValue>
        </StackDataContainer>
        <StackDataContainer>
          <Label>APR</Label>
          <Value>{apr}%</Value>
        </StackDataContainer>
        <StackDataContainer>
          <Label>Total Staked</Label>
          <Value>{`${totalStaked} USD`}</Value>
        </StackDataContainer>
        <SmallStackDataContainer onClick={() => setShowMore(!showMore)}>
          <Details>Details</Details>

          {showMore ? <BlackChevronUp /> : <BlackChevronDown />}
        </SmallStackDataContainer>
      </FormInfo>
      {showMore && !showModal && (
        <DetailsContainer>
          <FarmsDetails>
            <FarmDetailsData>
              <FarmDetailsRow inactive={inactive}>ESEED Earned:</FarmDetailsRow>
              <FarmDetailsRow inactive={inactive} colored>
                {getBalanceString(eseedEarned)}
              </FarmDetailsRow>
              {!inactive && (
                <FarmDetailsRow inactive={inactive} small>
                  { formatNumber(getBalanceAmount(eseedEarned) * eseedPrice) }~ USD
                </FarmDetailsRow>
              )}
            </FarmDetailsData>
            <FarmDetailsActions>
              <FormActionButton
                inactive={harvestLoading}
                onClick={() => {
                  HandleDeposit(0)
                }}
              >
                {harvestLoading ? 'Pending...' : 'Harvest'}
              </FormActionButton>
            </FarmDetailsActions>
          </FarmsDetails>
          <FarmsDetails>
            <FarmDetailsData>
              <FarmDetailsRow inactive={inactive} small>
                {tokenOverride || `${token1}-${token2}`} LP Staked
              </FarmDetailsRow>
              <FarmDetailsRow inactive={inactive}>{getBalanceString(stakingBalance)}</FarmDetailsRow>
            </FarmDetailsData>
            {!farmEnabled ? (
              <FarmDetailsActions>
                <FormActionButton inactive={farmEnabledPending} onClick={HandleEnable}>{farmEnabledPending ? 'Pending' : 'Enable'}</FormActionButton>
              </FarmDetailsActions>
            ) : (
              <FarmDetailsActions>
                <FormActionButton small onClick={onDeposit}>
                  +
                </FormActionButton>
                <FormActionButton small onClick={onWithdraw}>
                  -
                </FormActionButton>
              </FarmDetailsActions>
            )}
          </FarmsDetails>
        </DetailsContainer>
      )}
    </FormContainer>
  )
}

export default Farm
